import React, { useEffect, useState, useContext } from "react";
import { Button, Nav } from 'react-bootstrap';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
import { 재고context } from './App.js';
import {CSSTransition} from 'react-transition-group';
import {connect, useSelector, useDispatch} from 'react-redux';

let 박스 = styled.div`
padding: 20px;
`;

let 제목 = styled.h4`
font-size: 25px;
color: ${ props => props.색상 }
`;

function Detail(props) {

  let [alert, setAlert] = useState(true);
  let [스위치, 스위치변경] = useState(true);
  let [누른탭, 누른탭변경] = useState(0);
  let [inputData, inputData변경] = useState(0);

  useEffect(() => {
    setTimeout(() => { setAlert(false); }, 2000)
  },[alert, inputData])

  let 재고 = useContext(재고context);
  let history = useHistory();
  let {id} = useParams();
  let 찾은상품 = props.shoes.find((상품) => {
    return 상품.id == id
  });

  let dispatch = useDispatch()
  

  useEffect(() => {
    var arr = localStorage.getItem('watched');
    if(arr == null) {
      arr = [];
    } else { arr = JSON.parse(arr) }

      arr.push(id);
      arr = new Set(arr);
      arr = [...arr];
      localStorage.setItem('watched', JSON.stringify(arr));
    },[]);

  return (
    <div className='container'>
      <박스>
        <제목 색상={'rgb(71, 20, 20)'}>상세페이지</제목>
      </박스>

      { alert === true
        ? <div className="my-alert">
          <p>재고가 얼마 남지 않았습니다.</p>
        </div>
        : null
      }

      <div className='row'>
        {/* 상세페이지 이미지 */}
        <div className='col-md-5'>
          <img src={"https://codingapple1.github.io/shop/shoes" + (찾은상품.id+1) + ".jpg"} width="100%" />
        </div>

        {/* 상세페이지 정보 */}
        <div className='col-md-5'>
          <h3 className='pt-5'>{ 찾은상품.title }</h3>
          <p>{ 찾은상품.content }</p>
          <p>{ 찾은상품.price }원</p>
          <Info 재고={재고} 찾은상품={찾은상품}></Info>
          {/* <input type="number" placeholder="수량" onChange={(e)=>{
            inputData변경(e.target.value)
          }} /> */}
          <p></p>

          <Button variant="danger"
          onClick={()=>{ 
            dispatch( {type:'항목추가', 데이터: {id: 찾은상품.id, name: 찾은상품.title, quan:1}}) 
          history.push('/cart')
        }}>주문하기</Button>
          <Button onClick={() => { history.push('/') }} variant="danger">뒤로가기</Button>
        </div>

        {/* 최근 본 상품 */}
        <최근본상품 shoes={props.shoes} />
        
      </div> 

      <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={() => { 스위치변경(false); 누른탭변경(0)}}>Active</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={() => { 스위치변경(false); 누른탭변경(1)}}>Option 2</Nav.Link>
        </Nav.Item>
      </Nav>

      <CSSTransition in={스위치} classNames="wow" timeout={500}>
        <Tabcontent 누른탭={누른탭} 스위치변경={스위치변경} />
      </CSSTransition>

    </div>
  )
}

function 최근본상품(props) {
  let arr= localStorage.getItem('watched');
  arr = JSON.parse(arr);
  
  if(arr == null) { 
    return null
  } else {
  return (
    <div className="col-md-2">
      <h6>최근 본 상품</h6>
        {
          arr.map((a,i) =>{
            return(
              <div i={i} key={i}>
              <img src={"https://codingapple1.github.io/shop/shoes"+ (props.shoes[a].id+1) +".jpg"} width="60%" />
              <p>{props.shoes[a].title}</p>
              </div>
            )
          })
        }
    </div>
    )
  }
}

function Tabcontent(props) {
  
  useEffect(()=>{
    props.스위치변경(true);
  })

  if(props.누른탭 === 0) {
    return <div>0번째 내용입니다.</div>
  } else if (props.누른탭 === 1) {
    return <div>1번째 내용입니다.</div>
  }
}

function Info(props) {
  return <p>재고: {props.재고[props.찾은상품.id]} </p>
}

export default Detail

